<template>
  <div class="create-post">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
        <span>/</span>
        <span>发布话题</span>
      </template>
      <template #content>
        <form action>
          <div>
            <span>选择板块:</span>
            <select v-model="post.tab" name="tab" id="tab-value">
              <option value="ask">问答</option>
              <option value="share">分享</option>
              <option value="job">招聘</option>
              <option value="dev">客户端测试</option>
            </select>
          </div>
          <div>
            <input v-model="post.title" type="text" />
          </div>
          <div>
            <mavon-editor
              ref="mavonEditor"
              defaultOpen="edit"
              :subfield="false"
              :toolbars="toolbars"
              v-model="post.content"
            />
          </div>
          <el-button @click="submit" type="primary">提交</el-button>
        </form>
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../containers/Panel";
import axios from "../plugins/axios";
export default {
  components: {
    Panel
  },
  data() {
    return {
      post: {
        title: "",
        tab: "",
        content: ""
      },
      toolbars: {
        bold: false, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: false, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true, // 预览
      },
    };
  },
  methods: {
    async submit() {
      let { title, tab, content } = this.post;
      const accesstoken = localStorage.getItem("token");
      const res = await axios.post("/topics", {
        tab,
        title,
        content,
        accesstoken
      });
      this.$router.push(`/post/${res.topic_id}`);
    }
  }
};
</script>

<style>
select {
  width: 220px;
  border: 1px solid #ccc;
  height: 30px;
  line-height: 30px;
  margin-bottom: 5px;
}
input{
  width: 50%;
  height: 30px;
  margin-bottom: 10px;
}
</style>