<template>
  <Panel>
    <template #header>
      <router-link to="/">主页</router-link>
      <span>/</span>
      <span>登录</span>
    </template>
    <template #content>
      <el-form
        :model="ruleForm"
        :rules="rules"
        hide-required-asterisk
        ref="ruleForm"
        label-width="100px"
      >
        <el-form-item label="用户名" prop="username">
          <el-input
            type="text"
            v-model="ruleForm.username"
            autocomplete="off"
            style="width:200px"
            placeholder="用户名随意输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input
            type="password"
            v-model="ruleForm.password"
            autocomplete="off"
            style="width:200px"
            placeholder="密码输入用户token"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login">登录</el-button>
        </el-form-item>
      </el-form>
    </template>
  </Panel>
</template>

<script>
import Panel from "../containers/Panel";
import { mapActions, mapState } from "vuex";
export default {
  components: {
    Panel
  },
  data() {
    return {
      ruleForm: {
        username: "",
        password: "62291fae-487a-4cc1-9ebf-56a0a0e0ae08"
      },
      rules: {
        username: [
          { required: true, trigger: "blur", message: "用户名不能为空" }
        ],
        password: [{ required: true, trigger: "blur", message: "密码不能为空" }]
      }
    };
  },
  methods: {
    ...mapActions(["getInfo"]),
    login() {
      this.$refs.ruleForm.validate(boolen => {
        if (boolen) {
          this.getInfo(this.ruleForm.password)
            .then(() => {
              this.$router.push("/");
            })
            .catch(() => {
              this.$message.error("错了，token有误");
            });
        }
      });
    }
  }
};
</script>

<style lang='less'>
.panel {
  .panel-header {
    background-color: #f5f5f5;
    span{
      padding:0 5px 0 5px;
    }
  }
}
</style>