// vuex 文章列表模块
import axios from '../../plugins/axios'

export default {
    state: () => ({
        list: []
    }),
    mutations: {
        getPost(state, list) {
            state.list = list
        },
        loadMorePosts(state, list) {
            state.list = [...state.list, ...list]
        }
    },
    actions: {
        async getPost({ commit }, query) {
            const { page, tab } = query

            const queryStr = page ? `?tab=${tab}&page=${page}&limit=20` : `?tab=${tab}`

            const list = await axios.get(`/topics${queryStr}`)
            // console.log(list.data)
            page ? commit('loadMorePosts', list.data) : commit('getPost', list.data)
        },
    },

}

