import axios from '../../plugins/axios'

export default {
    state: () => ({
        user: null
    }),
    mutations: {
        getPostUser(state, user) {
            state.user = user;
        }
    },
    actions: {
        async getPostUser({ commit }, loginname) {
            const user = await axios.get(`/user/${loginname}`)
            // console.log(user)
            commit('getPostUser', user.data);
        }
    },
}