<template>
  <div class="post-item">
    <router-link :to="`/user/${author.loginname}`">
      <span class="avatar">
        <img :src="author.avatar_url" alt />
      </span>
    </router-link>

    <div v-if="reply_count" class="count">
      <span class="reply-count">{{reply_count}}</span>
      <span>/</span>
      <span class="visit-count">{{visit_count}}</span>
    </div>
    <span v-if="isHasTag" :class="['tabs',{active:top || good}]">{{tagText}}</span>

    <h3>
      <router-link :to="{name:'postItem', params:{id}}">{{title}}</router-link>
    </h3>

    <span class="last-reply-count">{{last_reply_at | moment("from", "now")}}</span>
  </div>
</template>

<script>
import Vue from "vue";
export default {
  props: [
    "author",
    "reply_count",
    "visit_count",
    "tab",
    "title",
    "last_reply_at",
    "top",
    "good",
    "id"
  ],
  computed: {
    isHasTag() {
      const page = this.$route.path;
      const isAllOrGoodPage = (page === "/" || page === "all" || page === "good");
      return this.top ? true : isAllOrGoodPage ? true : false;
    },
    tagText() {
      return this.top
        ? "置顶"
        : this.good
        ? "精华"
        : Vue.filter("transformTab")(this.tab);
    }
  }
};
</script>

<style lang='less'>
.post-item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ccc;
  padding: 10px 0;
  &:hover {
    background-color: rgb(228, 222, 222);
  }
  .last-reply-count{
    flex-shrink: 0;
  }
  .count {
    flex-shrink: 0;
    width: 80px;
    margin: 0 10px;
    text-align: center;
    .reply-count {
      color: #9e78c0;
    }
    .visit-count {
      color: #b4b4b4;
      font-size: 10px;
    }
  }

  .tabs {
    flex-shrink: 0;
    background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    color: #fff;
    font-size: 12px;
    margin-right: 10px;
    &.active{
      color: #fff;
      background-color: #80bd01;
    }
  }
  h3 {
    margin: 0;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 16px;
    color: #333;
    flex-grow: 1;
    margin-left: 10px;
    font-weight: normal;
    a {
      color: #333;
      &:visited {
        color: #ccc;
      }
    }
  }
  .avatar {
    flex-shrink: 0;
    img {
      width: 30px;
      height: 30px;
    }
  }
}
</style>