<template>
  <div v-if="postList.length">
    <PostItem v-for="item in postList" :key="item.id" v-bind="item" />
    <div v-if="loading" class="loading">加载中...</div>
  </div>
</template>

<script>
import PostItem from "../components/PostItem";
import debounce from "lodash/debounce";
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      page: 2,
      loading: false
    };
  },
  components: {
    PostItem
  },
  computed: {
    ...mapState({
      postList: state => state.postList.list,
      tab() {
        return this.$route.params.tab || "all";
      }
    })
  },
  watch: {
    "$route.params.tab": {
      handler() {
        // console.log(value)
        // 在刷新的时候判断当前的类别是不是我们网站应有的类别
        // console;
        if (["all", "ask", "share", "job", "dev", "good"].includes(this.tab)) {
          this.getPost({ tab: this.tab });
          this.page = 2;
        } else {
          this.$router.push({ name: "err" });
        }
      },
      immediate: true
    }
  },
  methods: {
    ...mapActions(["getPost"]),
    scrollFun: debounce(async function() {
      const scrollHeight = document.documentElement.scrollHeight;
      const windowHeight =
        window.innerHeight || document.documentElement.clientHeight;
      const scrollTop = window.scrollY || document.documentElement.scrollTop;
      if (
        scrollHeight - windowHeight - scrollTop <= 200 &&
        this.postList.length
      ) {
        if (!this.loading) {
          this.loading = true;
          this.page++;
          await this.getPost({ tab: this.tab, page: this.page });
          this.loading = false;
        }
      }
    }, 300)
  },
  mounted() {
    window.addEventListener("scroll", this.scrollFun);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrollFun);
  }
};
</script>

<style lang='less'>
.loading {
  height: 100px;
  text-align: center;
  line-height: 100px;
  font-size: 20px;
}
</style>