<template>
  <div class="home">
    <Panel class="list">
      <template #header>
        <router-link
          active-class="active"
          :class="['tab',{active:tab === '全部' && $route.path === '/'}]"
          :to="tab | transformTab"
          v-for="tab in tabs"
          :key="tab"
        >{{tab}}</router-link>
      </template>

      <template #content>
        <router-view></router-view>
      </template>
    </Panel>
    <Panel class="user" v-if="info">
      <template #header>
        <p class="personal">个人信息</p>
      </template>
      <template #content>
        <div class="username">
          <div>
            <img :src="info.avatar_url" alt />
            <span>{{info.loginname}}</span>
          </div>
        </div>
        <router-link to="post/create">
          <el-button type="success" size="small">发布话题</el-button>
        </router-link>
      </template>
    </Panel>
    <Panel class="user" v-else>
      <template #header>
        <p class="personal">个人信息</p>
      </template>
      <template #content>
        <h2>约克论坛</h2>
        <router-link to="/login">
          <el-button type="success" size="small">请登录</el-button>
        </router-link>
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../containers/Panel";
import { mapState } from "vuex";
export default {
  data() {
    return {
      tabs: ["全部", "精华", "分享", "问答", "招聘", "客户端测试"]
    };
  },
  components: {
    Panel
  },
  computed: {
    ...mapState({
      info: state => state.login.info
    })
  }
};
</script>

<style>
.home {
  display: flex;
  justify-content: space-between;
}
.home .panel .panel-header {
  background-color: rgb(231, 65, 65);
}
a.tab {
  color: #fff;
  margin-right: 15px;
  font-size: 14px;
  padding: 2px;
}
a.active {
  background-color: #fff;
  color: #e74141;
  border-radius: 3px;
}
.user {
  width: 350px;
  text-align: center;
}
.username {
  margin-bottom: 15px;
}
.username img {
  width: 48px;
  height: 48px;
  margin: 5px 5px 0 5px;
}
.personal {
  color: #fff;
  margin: 0;
}
</style>