<template>
  <div v-if="post">
    <Panel>
      <template #header>
        <h2>
          <span class="tag" v-if="post.top || post.good">{{post.top ? '置顶' : '精华'}}</span>
          {{post.title}}
        </h2>
        <p>
          <span>·</span>
          <span>发布于{{post.create_at | moment("from", "now")}}</span>
          <span>·</span>
          <span>作者{{post.author.loginname}}</span>
          <span>·</span>
          <span>{{post.visit_count}}次浏览</span>
          <span>·</span>
          <span>来自{{post.tab | transformTab}}</span>
        </p>
        <el-button
          @click="collect"
          size="mini"
          :type=" post.is_collect ? 'info' : 'success'"
          v-if="info"
        >
          {{
          post.is_collect ? "取消收藏" : "收藏"
          }}
        </el-button>
      </template>
      <template #content>
        <div class="post-content" v-html="post.content"></div>
      </template>
    </Panel>
    <panel>
      <template #header>
        <span>{{post.reply_count}}回复</span>
      </template>
      <template v-if="post.replies.length" #content>
        <PostCommentItem
          v-for="(comment,index) in post.replies"
          :key="comment.id"
          v-bind="{...comment,index}"
        />
      </template>
    </panel>

    <Panel v-if="info">
      <template #header>
        <span>添加回复</span>
      </template>
      <template #content>
        <mavon-editor ref="mavonEditor" defaultOpen="edit" :subfield="false" :toolbars="toolbars" />
        <el-button type="primary" @click="add">回复</el-button>
      </template>
    </Panel>
  </div>
</template>

<script>
import PostCommentItem from "./PostCommentItem";
import Panel from "../containers/Panel";
import { mapActions, mapState } from "vuex";

export default {
  components: {
    Panel,
    PostCommentItem
  },
  data() {
    return {
      content: "",
      toolbars: {
        bold: false, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: false, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true // 预览
      }
    };
  },
  computed: {
    ...mapState({
      post: state => state.post.post,
      info: state => state.login.info
    })
  },
  created() {
    this.getPostItem(this.$route.params.id);
  },
  methods: {
    ...mapActions(["getPostItem", "collectHandle"]),
    collect() {
      this.collectHandle({
        id: this.post.id,
        is_collect: !this.post.is_collect
      });
    },
    add() {
      if (this.content.trim()) {
        this.addComment({ id: this.post.id, content: this.content });
      }
    }
  }
};
</script>

<style lang='less'>
.post-header {
  border-bottom: 1px solid #ccc;
  > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .tag {
    font-size: 12px;
    color: #fff;
    padding: 2px 4px;
    background-color: #80bd01;
    border-radius: 3px;
  }
  .disc {
    margin: 0 10px;
    font-weight: bold;
  }
  p {
    font-size: 14px;
    color: #999;
  }
}
.post-content {
  img {
    max-width: 100%;
  }
}
</style>