<template>
  <header>
    <div class="header-inner">
      <h1>
        <router-link to="/">
          <img src="../assets/logo1.png" alt />
        </router-link>
      </h1>
      <div class="header-right">
        <router-link to="/">首页</router-link>
        <router-link to="/">新手入门</router-link>
        <router-link to="/">API</router-link>
        <router-link to="/">关于</router-link>
        <router-link v-if="!info" to="/login">登录</router-link>
        <div v-else>
          <span @click="logout">退出</span>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState({
      info: state => state.login.info
    })
  },
  methods: {
    ...mapMutations(["getInfo"]),
    logout() {
      if (this.$route.path !== "/") {
        this.$router.push("/");
      }
      localStorage.removeItem("info");
      localStorage.removeItem("token");
      this.getInfo(null);
    }
  }
};
</script>

<style lang='less'>
header {
  background-color: #E74141;
  .header-inner {
    height: 50px;
    display: flex;
    width: 1400px;
    justify-content: space-between;
    width: 90%;
    height: 80px;
    margin: 0 auto;
    align-items: center;
    h1 {
      width: 120px;
      margin: 0;
      img {
        height: 80px;
        width: 90px;
      }
    }
  }
  .header-right {
      display: flex;
      align-items: center;
      >div{
        display: flex;
        align-items: center;
        color: #fff;
        margin-left: 20px;
        font-size: 12px;
        span{
          margin-right: 20px;
          cursor: pointer;
        }
      }
      a {
        color: #fff;
        margin-left: 20px;
        font-size: 14px;
      }
    }
  }
</style>